@import './index.css';


$mainColor : thistle;

.main  {
    color: $mainColor
};

.red {
    color:red;
};

div.container {
    h4{
        color : $mainColor
    }
}

@mixin fun() {
    background : #eeeeee;
    padding : 15px;
    border-radius : 5px;
    max-width : 500px;
    width : 100%;
    margin : auto;
}

.my-alert {
    @include fun();
    p{
        margin-bottom : 0;
    }
}

.my-alert2 {
    @extend .my-alert;
    background : yellow;
}
